'use strict';

var nodeify = require('promise-nodeify');

var moduleConfig = require('../utils/module-config');

var _require = require('../lib/converters'),
    collectify = _require.collectify;

module.exports = function (arg, config) {
  var send = moduleConfig(arg);

  var _rm = require('./rm-async-iterator')(config);

  return {
    get: require('./get')(send),
    stat: require('./stat')(send),
    put: require('./put')(send),
    rm: function rm(input, options, callback) {
      if (typeof options === 'function') {
        callback = options;
        options = {};
      }

      return nodeify(collectify(_rm)(input, options), callback);
    },
    _rmAsyncIterator: _rm
  };
};