'use strict';

var promisify = require('promisify-es6');

var concatStream = require('concat-stream');

var once = require('once');

var SendFilesStream = require('../utils/send-files-stream');

module.exports = function (send) {
  var sendFilesStream = SendFilesStream(send, 'files/write');
  return promisify(function (pathDst, _files, opts, _callback) {
    if (typeof opts === 'function' && !_callback) {
      _callback = opts;
      opts = {};
    } // opts is the real callback --
    // 'callback' is being injected by promisify


    if (typeof opts === 'function' && typeof _callback === 'function') {
      _callback = opts;
      opts = {};
    }

    var files = [].concat(_files);
    var callback = once(_callback);
    var options = {
      args: pathDst,
      qs: opts
    };
    var stream = sendFilesStream({
      qs: options
    });
    var concat = concatStream(function (result) {
      return callback(null, result);
    });
    stream.once('error', callback);
    stream.pipe(concat);
    files.forEach(function (file) {
      return stream.write(file);
    });
    stream.end();
  });
};