'use strict';

var _require = require('readable-stream'),
    Duplex = _require.Duplex;

var eachSeries = require('async/eachSeries');

var isStream = require('is-stream');

var once = require('once');

var prepareFile = require('./prepare-file');

var Multipart = require('./multipart');

function headers(file, i) {
  var filename = file.path ? encodeURIComponent(file.path) : '';
  var header = {
    'Content-Disposition': "form-data; name=\"data".concat(i, "\"; filename=\"").concat(filename, "\"")
  };

  if (!file.content) {
    header['Content-Type'] = 'application/x-directory';
  } else if (file.symlink) {
    header['Content-Type'] = 'application/symlink';
  } else {
    header['Content-Type'] = 'application/octet-stream';
  }

  return header;
}

module.exports = function (send, path) {
  return function (options) {
    var ended = false;
    var writing = false;
    options = options ? Object.assign({}, options, options.qs) : {};
    var multipart = new Multipart();
    var retStream = new Duplex({
      objectMode: true
    });

    retStream._read = function (n) {};

    retStream._write = function (file, enc, _next) {
      var next = once(_next);

      try {
        var files = prepareFile(file, options).map(function (file, i) {
          return Object.assign({
            headers: headers(file, i)
          }, file);
        });
        writing = true;
        eachSeries(files, function (file, cb) {
          return multipart.write(file, enc, cb);
        }, function (err) {
          writing = false;

          if (err) {
            return next(err);
          }

          if (ended) {
            multipart.end();
          }

          next();
        });
      } catch (err) {
        next(err);
      }
    };

    retStream.once('finish', function () {
      if (!ended) {
        ended = true;

        if (!writing) {
          multipart.end();
        }
      }
    });
    var qs = options.qs || {};
    qs['cid-version'] = propOrProp(options, 'cid-version', 'cidVersion');
    qs['raw-leaves'] = propOrProp(options, 'raw-leaves', 'rawLeaves');
    qs['only-hash'] = propOrProp(options, 'only-hash', 'onlyHash');
    qs['wrap-with-directory'] = propOrProp(options, 'wrap-with-directory', 'wrapWithDirectory');
    qs.pin = propOrProp(options, 'pin');
    qs.preload = propOrProp(options, 'preload');
    qs.hash = propOrProp(options, 'hash', 'hashAlg');

    if (options.strategy === 'trickle' || options.trickle) {
      qs.trickle = 'true';
    }

    var args = {
      path: path,
      qs: qs,
      args: options.args,
      multipart: true,
      multipartBoundary: multipart._boundary,
      stream: true,
      recursive: true,
      progress: options.progress
    };
    multipart.on('error', function (err) {
      retStream.emit('error', err);
    });
    var request = send(args, function (err, response) {
      if (err) {
        return retStream.emit('error', err);
      }

      if (!response) {
        // no response, which means everything is ok, so we end the retStream
        return retStream.push(null); // early
      }

      if (!isStream(response)) {
        retStream.push(response);
        retStream.push(null);
        return;
      }

      response.on('error', function (err) {
        return retStream.emit('error', err);
      });

      if (options.converter) {
        response.on('data', function (d) {
          if (d.Bytes && options.progress) {
            options.progress(d.Bytes);
          }
        });
        var Converter = options.converter;
        var convertedResponse = new Converter();
        convertedResponse.once('end', function () {
          return retStream.push(null);
        });
        convertedResponse.on('data', function (d) {
          return retStream.push(d);
        });
        response.pipe(convertedResponse);
      } else {
        response.on('data', function (d) {
          if (d.Bytes && options.progress) {
            options.progress(d.Bytes);
          }

          retStream.push(d);
        });
        response.once('end', function () {
          return retStream.push(null);
        });
      }
    }); // signal the multipart that the underlying stream has drained and that
    // it can continue producing data..

    request.on('drain', function () {
      return multipart.emit('drain');
    });
    multipart.pipe(request);
    return retStream;
  };
};

function propOrProp(source, prop1, prop2) {
  if (prop1 in source) {
    return source[prop1];
  } else if (prop2 in source) {
    return source[prop2];
  }
}